import { Howl } from 'howler';

/**
 * Howl
 * ! 
 * @param {Object} o - data(base64 String)format(string)
 */

Howl.prototype.changeMusic = function(o) {
  var self = this;
  self.unload(true);
  self._duration = 0;
  self._sprite = {};
  self._src = typeof o.data !== 'string' ? o.data : [o.data];
  self._format = typeof o.format !== 'string' ? o.format : [o.format];
};

class HowlAudio {
  /**
   *
   * @param {Object} music - data(base64 String)format(string)
   * @param {Function} updateTimeFunc - (vuex)
   * @param {Function} updateTimeEndFunc - (vuex)
   */

  constructor(music, updateIsPlay, updateTimeFunc, updateTimeEndFunc) {
    this.howl = new Howl({
      src: music.data,
      format: music.format,
      preload: false,
      autoplay: false,
      loop: false,
      volume: 1
    });

    this.updateIsPlay = updateIsPlay;
    this.updateTimeFunc = updateTimeFunc;
    this.updateTimeEndFunc = updateTimeEndFunc;
  }

  /**
   * @return {Howl} - Howl
   */

  getHowl() {
    return this.howl;
  }

  // 
  init() {
    this.howl.load();
    this.howl.on('load', () => {
      this.updateTimeEndFunc(this.howl.duration());
    });
  }

  /**
   * 
   * @param {Object} music - data(base64 String)format(string)
   */

  changeMusic(music) {
    this.resetTimer();
    this.howl.changeMusic(music);
  }

  // timer
  setTimer() {
    this.timer = setInterval(() => {
      this.updateTimeFunc(this.howl.seek());
    }, 200);
  }

  // timer
  resetTimer() {
    clearInterval(this.timer);
    delete this.timer;
  }

  /**
   * 
   * @param {Boolean} isLoop - 
   */

  play(isLoop) {
    this.howl.play();
    // 
    this.howl.on('play', () => {
      this.setTimer();
    });
    // 
    this.howl.on('end', () => {
      if (!isLoop) {
        this.stop();
      }
    });
  }

  // 
  stop() {
    this.resetTimer();
    this.updateIsPlay();
    this.howl.stop();
  }

  // UI()
  pause() {
    this.resetTimer();
    this.howl.pause();
  }

  /**
   * ()
   * @param {Number} time - (s)
   */

  updateTime(time) {
    this.howl.seek(time);
  }

  /**
   * ()
   * @param {Number} vol - (0~1)
   */

  updateVolume(vol) {
    this.howl.volume(vol);
  }

  // 
  loop() {
    const isloop = !this.howl.loop();
    this.howl.loop(this.howl.loop(isloop));
  }
}

export { HowlAudio };
