<template>
  <div class="AudioController">
    <div class="ctrl">
      <!---->
      <span v-show="!isVisual">
        <base-button
          class="base-button--sm"
          :img-url="require('@/assets/ui-icon/visualizer.svg')"
          @click="visual"
        />
      </span>
      <span v-show="isVisual">
        <base-button
          class="base-button--sm"
          :img-url="require('@/assets/ui-icon/visualizer-true.svg')"
          @click="visual"
        />
      </span>
      <span v-show="!isLoop">
        <base-button
          class="base-button--sm"
          :img-url="require('@/assets/ui-icon/loop.svg')"
          @click="loop"
        />
      </span>
      <span v-show="isLoop">
        <base-button
          class="base-button--sm"
          :img-url="require('@/assets/ui-icon/loop-true.svg')"
          @click="loop"
        />
      </span>
      <base-button
        class="base-button--sm"
        :img-url="require('@/assets/ui-icon/shuffle.svg')"
        @click="shuffle"
      />
      <!---->
      <div class="ctrl__seek">
        <base-time-range :seek-info="seekInfo" @update-time="updateTime" />
      </div>
      <!---->
      <base-button
        class="base-button--lg"
        :img-url="require('@/assets/ui-icon/prev.svg')"
        @click="prev"
      />
      <!---->
      <span v-show="!isPlay">
        <base-button
          class="base-button--lg"
          :img-url="require('@/assets/ui-icon/play.svg')"
          @click="play"
        />
      </span>
      <!---->
      <span v-show="isPlay">
        <base-button
          class="base-button--lg"
          :img-url="require('@/assets/ui-icon/stop.svg')"
          @click="pause"
        />
      </span>
      <!---->
      <base-button
        class="base-button--lg"
        :img-url="require('@/assets/ui-icon/next.svg')"
        @click="next"
      />
      <!---->
      <div class="ctrl__seek">
        <base-volume-range :volume="volume" @update-volume="updateVolume" />
      </div>
    </div>
  </div>
</template>

<script>
import BaseTimeRange from '../atoms/Range/BaseTimeRange';
import BaseVolumeRange from '../atoms/Range/BaseVolumeRange';
import BaseButton from '../atoms/BaseButton';

export default {
  name: 'AudioController',
  components: {
    'base-time-range': BaseTimeRange,
    'base-volume-range': BaseVolumeRange,
    'base-button': BaseButton
  },
  props: {
    isPlay: {
      type: Boolean,
      default: false,
      required: true
    },
    isLoop: {
      type: Boolean,
      default: false,
      required: true
    },
    isVisual: {
      type: Boolean,
      default: false,
      required: true
    },
    seekInfo: {
      type: Object,
      default: () => {},
      required: true
    },
    volume: {
      type: Number,
      default: 1,
      required: true
    }
  },
  methods: {
    updateTime(time) {
      this.$emit('update-Time', time);
    },
    updateVolume(vol) {
      this.$emit('update-volume', vol);
    },
    next() {
      this.$emit('next');
    },
    prev() {
      this.$emit('prev');
    },
    play() {
      this.$emit('play');
    },
    pause() {
      this.$emit('pause');
    },
    visual() {
      this.$emit('visual');
    },
    loop() {
      this.$emit('loop');
    },
    shuffle() {}
  }
};
</script>

<style scoped>
/* css */

.ctrl {
  text-align: center;
  margin: 60px auto;
  padding: 0;
  height: 150px;
  width: 95%;
  border: 0.1px solid white;
}

.ctrl__seek {
  margin: 0;
  padding: 0;
  text-align: center;
}

/* css */
